}

body {
  color: rgba(255, 255, 123, 1);
  color: var(--text);
  background-color: rgba(35,144,214,1);
  background-color: var(--stratospheric-blue-01);
  font-size: clamp(100%, 1rem + 0.25vw, 2rem);
  display: grid;
  grid-template-rows: auto 1fr auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

footer {
	font-size: 0.59rem;
	font-size: var(--smText);
	text-align: center;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: rgba(255, 214, 58, 1);
  color: var(--caramel-02);
  font-weight: 500;
  line-height: 1.2;
}

h1 {
  font-size: clamp(2rem, 2rem + 1vw, 3rem);
  font-family: 'Delius Swash Caps', cursive;
  text-shadow: 1.5px 2px black;
}

h2 {
  font-size: clamp(1.5rem, 1.5rem + 1vw, 2.5rem);
}

h3 {
  font-size: clamp(1.25rem, 1.25rem + 0.75vw, 2.25rem);
}

input,
textarea,
button {
  border: 1px solid var(--heading);
  border-radius: 0.25rem;
}

input:focus,
textarea:focus {
  outline: none;
  box-shadow: 0 0 0 3px var(--yellow);
}

button,
input[type='submit'] {
  cursor: pointer;
  /* color: var(--lightBlue); */
  /* background: var(--red); */
  /* border: 2px solid var(--red); */
  padding: 0.25rem 1.5rem;
  padding: var(--btnPadding);
  transition: 0.2s all ease-in;
  transition: var(--transition);
  text-transform: uppercase;
  font-size: 1rem;
  font-size: var(--lgText);
  box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.25);
  box-shadow: var(--smShadow);
}

button:focus,
input[type='submit']:focus {
  /* box-shadow: var(--focusShadow); */
}

ul,
li {
  list-style: none;
}

a {
  color: rgba(255, 255, 201, 1);
  color: var(--yellow-01);
  text-decoration: none;
  border-bottom: 2px solid transparent;
  transition: 0.2s all ease-in;
  transition: var(--transition);
}



