.custom-button{width:auto;letter-spacing:0.5px;padding:3px 5px 3px 5px;font-size:var(--smText);background-color:var(--ocean-green-00);color:var(--text);text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:2px solid var(--leather-02);cursor:pointer}.custom-button:hover{background-color:var(--ocean-green-04);color:var(--text);border:2px solid var(--leather-02)}.custom-button.google-sign-in{background-color:#4285f4;color:var(--leather-02)}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}




.header-link{display:flex;flex-direction:column;align-items:center;width:3rem;height:3rem;border:3.5px solid var(--leather-02);border-radius:100%;margin:0 var(--xsPadding);background-color:var(--ocean-green-00)}.header-link:hover{cursor:pointer;background-color:var(--ocean-green-04)}.header-link:hover .logo{transform:scale(1.1);transition:transform 0.5s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.header-link .logo{width:2.5em;height:2.5em}.header-link .label-container{display:flex;text-shadow:1.5px 2px var(--stratospheric-blue-04);height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:2px;margin-top:3px;position:relative;top:0}.header-link .label-container .header-link-label{color:var(--yellow-00);font-size:var(--smText)}

.header-menu{display:flex;justify-content:center;margin:0 0.09rem}

.header{width:100%;display:flex;flex-direction:column;padding-bottom:17px;margin-bottom:25px;border:3px dashed var(--leather-02);border-radius:25px;background-color:var(--ocean-green-03)}.header .sitetitle h1{font-size:var(--lgHeading)}

.menu-item{display:flex;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;justify-content:center;border:3.5px solid var(--leather-04);border-radius:100%;background-color:var(--leather-00);margin:0 7.5px 40px}.menu-item:nth-last-child(-n+2){justify-self:flex-end}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{transform:scale(1.1);transition:transform 0.5s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:1}.menu-item .background-image{width:4rem;height:4rem}.menu-item .menu-item-content{height:90px;text-shadow:1.5px 2px var(--stratospheric-blue-04);display:flex;flex-direction:column;align-items:center;justify-content:center;position:absolute}.menu-item .menu-item-content .title{font-weight:bold;margin-bottom:0px;font-size:var(--mdText);position:relative;top:18px}.menu-item .menu-item-content .subtitle{font-weight:lighter;font-size:var(--smText)}

.directory-menu{display:grid;grid-template-columns:1fr 1fr;width:24ch}

.startpage{display:flex;justify-content:center;padding:10px 0}



.group{position:relative;margin:0}.group .form-input{background:none;background-color:white;color:grey;font-size:var(--smText);padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:15px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-12px;font-size:var(--xsText);color:var(--text)}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:var(--smText);font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-12px;font-size:var(--xsText);color:var(--text)}

.sign-in{width:50%;display:flex;flex-direction:column;justify-content:center;border:3px dashed var(--leather-02);border-radius:25px;background-color:var(--ocean-green-03);margin:0 5px;padding:0 var(--smPadding) 5px}.sign-in .title{margin:10px 0;font-size:var(--lgText)}.sign-in .subtext{font-size:var(--smText)}

.sign-up{width:50%;display:flex;flex-direction:column;justify-content:center;border:3px dashed var(--leather-02);border-radius:25px;background-color:var(--ocean-green-03);margin:0 5px;padding:0 var(--smPadding) 5px}.sign-up .title{margin:10px 0;font-size:var(--lgText)}.sign-up .subtext{font-size:var(--smText)}

.login-page{width:100%;display:flex;justify-content:space-evenly}

.footer{width:100%;margin-top:10px}.footer .footer-content{margin:auto;padding:5px;border:3px dashed var(--leather-02);border-radius:25px;background-color:var(--ocean-green-03);width:50ch}

